<template>
    <transition name="slideRight">
        <div class="loading">
            <div class="question">
                
                <form method="post">
                    <h2 v-text="question1" :class="{ tips:tips1 }"></h2>
                    <p>
                        <label><input type="radio" value="" v-model="answer1"></label>
                        <label><input type="radio" value="" v-model="answer1"></label>
                    </p>
                    <h2 v-text="question2" :class="{ tips:tips2 }"></h2>
                    <p>
                        <label><input type="radio" value="5" v-model="answer2">5</label>
                        <label><input type="radio" value="10" v-model="answer2">10</label>
                        <label><input type="radio" value="10" v-model="answer2">10</label>
                    </p>
                    <h2 v-text="question3" :class="{ tips:tips3 }"></h2>
                    <p>
                        <label><input type="radio" value="" v-model="answer3"></label>
                        <label><input type="radio" value="" v-model="answer3"></label>
                        <label><input type="radio" value="" v-model="answer3"></label>
                    </p>
                    <h2 v-text="question4" :class="{ tips:tips4 }"></h2>
                    <p>
                        <label><input type="radio" value="" v-model="answer4"></label>
                        <label><input type="radio" value="" v-model="answer4"></label>
                        <label><input type="radio" value="" v-model="answer4"></label>
                    </p>
                    <h2 v-text="question5" :class="{ tips:tips5 }"></h2>
                    <p>
                        <label><input type="radio" value="" v-model="answer5"></label>
                        <label><input type="radio" value="" v-model="answer5"></label>
                    </p>
                    <h2 v-text="question6" :class="{ tips:tips6 }"></h2>
                    <p>
                        <label><input type="radio" value="" v-model="answer6"></label>
                        <label><input type="radio" value="" v-model="answer6"></label>
                    </p>
                    <h2 v-text="question7" :class="{ tips:tips7 }"></h2>
                    <p>
                        <label><input type="radio" value="" v-model="answer7"></label>
                        <label><input type="radio" value="" v-model="answer7"></label>
                    </p>     
                </form>

            </div>
        </div>
    </transition>
</template>

<script>
export default {
    data () {
        return {
            question1: '',
            question2: '',
            question3: '',
            question4: '',
            question5: '',
            question6: '',
            question7: '',
            answer1: '',
            answer2: '',
            answer3: '',
            answer4: '',
            answer5: '',
            answer6: '',
            answer7: '',
            tips1: false,
            tips2: false,
            tips3: false,
            tips4: false,
            tips5: false,
            tips6: false,
            tips7: false,
            tips: 'tips',
            goContact: false
        };
    },
    methods: {
        send () {
            if (this.answer1 === '') {
                this.tips1 = true;
            } else {
                this.tips1 = false;
            };
            if (this.answer2 === '') {
                this.tips2 = true;
            } else {
                this.tips2 = false;
            };
            if (this.answer3 === '') {
                this.tips3 = true;
            } else {
                this.tips3 = false;
            };
            if (this.answer4 === '') {
                this.tips4 = true;
            } else {
                this.tips4 = false;
            };
            if (this.answer5 === '') {
                this.tips5 = true;
            } else {
                this.tips5 = false;
            };
            if (this.answer6 === '') {
                this.tips6 = true;
            } else {
                this.tips6 = false;
            };
            if (this.answer7 === '') {
                this.tips7 = true;
            } else {
                this.tips7 = false;
            };
            if (this.answer1 === '' || this.answer2 === '' || this.answer3 === '' || this.answer4 === '' || this.answer5 === '' || this.answer6 === '' || this.answer7 === '') {
                alert('');
            } else {
                Qudao51.questionnaire.answer(this.question1, this.answer1);
                Qudao51.questionnaire.answer(this.question2, this.answer2);
                Qudao51.questionnaire.answer(this.question3, this.answer3);
                Qudao51.questionnaire.answer(this.question4, this.answer4);
                Qudao51.questionnaire.answer(this.question5, this.answer5);
                Qudao51.questionnaire.answer(this.question6, this.answer6);
                Qudao51.questionnaire.answer(this.question7, this.answer7);
                Qudao51.questionnaire.finish();
                window.scrollTo(0, 0);
                this.goContact = true
            };
        }
    }
};
</script>



